@charset "UTF-8";

.leaflet-container {
  width: 100%;
  height: 100vh;
}
/* ///////////////////  time-footer    ///////////////////// */

.time-footer {
  position: absolute;
  bottom: 15px;
  left: 10px;
  color: white;
  font-size: 20px;
  z-index: 999;
  font-weight: bolder;

  background-color: transparent;
}
/* ///////////////////   new-user-onmap    ///////////////////// */

.new-user-onmap {
  stroke: rgb(0, 200, 100);
  animation: pulsate 1s ease-out;
  animation-iteration-count: infinite;
  opacity: 0;
}

@keyframes pulsate {
  0% {
    transform: opacity 0;
  }
  50% {
    opacity: 1;
  }
  100% {
    transform: opacity 0;
  }
}
/* ///////////////////   import audio      ///////////////////// */
.mediaRecord {
  padding-top: 100px;
  display: flex;
  align-items: center;
  justify-content: center;
}

/* ///////////////////   Leaftlet      ///////////////////// */

.leaflet-control-container
  .leaflet-top
  .leaflet-control-zoom
  a.leaflet-control-zoom-in {
  font-weight: bolder;
  height: 30px;
  width: 30px;
  font-size: 20px;
  border-radius: 12px;
  color: #495fef;
  background-color: transparent;
  border: 1px solid #495fef;
  display: flex;
  justify-content: center;
  align-items: center;
}
.leaflet-control-container
  .leaflet-top
  .leaflet-control-zoom
  a.leaflet-control-zoom-out {
  transform: translateY(8px);
  font-weight: bolder;
  height: 30px;
  width: 30px;
  background-color: transparent;
  font-size: 20px;
  color: #495fef;
  border-radius: 12px;
  border: 1px solid #495fef;
  display: flex;
  justify-content: center;
  align-items: center;
}

/* ///////////////////    Da Rulez      ///////////////////// */
.darulez-rod-container {
  position: absolute;
  background-color: transparent;
  top: 10px;
  width: 100vw;
  z-index: 900;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.rule-questions {
  font-size: 12px;
  font-weight: bolder;
  margin: 10px 0;
  display: block;
  white-space: wrap;
  overflow-y: hidden;
}
.rule-description {
  font-size: 10px;
  font-weight: bolder;
  margin: 10px 0;
  display: block;
  white-space: wrap;
  overflow-y: hidden;
}

.darulez-close {
  visibility: collapse;
  height: 0;
  width: 320px;
  transform: translateX(-162.5px);
  background-color: rgb(42, 42, 42);
  z-index: 1000;
  position: absolute;
  left: 50vw;
  top: 0;
  border-radius: 12px;
  transition: 700ms cubic-bezier(0.54, 0.21, 0.18, 1.35);
  opacity: 0.1;
}
.darulez-open {
  height: 500px;
  width: 320px;
  transform: translateX(-162.5px);
  background-color: rgb(42, 42, 42);
  z-index: 1000;
  position: absolute;
  left: 50vw;
  top: 200px;
  border-radius: 35px;
  transition: 700ms cubic-bezier(0.54, 0.21, 0.18, 1.35);
  opacity: 1;
}

.darulez-close-icon {
  opacity: 0.7;
  transition: 1s cubic-bezier(0.54, 0.21, 0.18, 1.35);
  font-size: "12px";
  padding: 4px;
  border-radius: 8px;
  background-color: rgb(42, 42, 42);
  transform: rotate(540deg);
}
.darulez-open-icon {
  font-size: "12px";
  padding: 4px;
  border-radius: 8px;
  background-color: rgb(42, 42, 42);
  opacity: 1;
  transition: 700ms cubic-bezier(0.54, 0.21, 0.18, 1.35);
}

/* ///////////////////    audio Player      ///////////////////// */

.audio-player-close {
  visibility: collapse;
  height: 50px;
  width: 50px;
  background-color: rgb(42, 42, 42);
  position: absolute;
  left: 10px;
  top: 10px;
  z-index: 999;
  border-radius: 12px;
  transition: 700ms cubic-bezier(0.54, 0.21, 0.18, 1.35);
  opacity: 0.1;
}
.audio-player-open {
  height: 97px;
  width: 325px;
  background-color: rgb(42, 42, 42);
  position: absolute;
  left: 5px;
  top: 85px;
  z-index: 1000;
  border-radius: 12px;
  transition: 700ms cubic-bezier(0.54, 0.21, 0.18, 1.35);
  opacity: 1;
}
.audio-player-icon-open {
  height: 50px;
  width: 50px;
  font-size: 20px;
  color: #495fef;
  background-color: rgb(42, 42, 42);
  position: absolute;
  left: 10px;
  top: 10px;
  z-index: 1000;
  border-radius: 12px;
  transition: 700ms cubic-bezier(0.54, 0.21, 0.18, 1.35);
  /* transform: rotate(180deg); */
  display: flex;
  align-items: center;
  font-weight: bolder;
  justify-content: center;
}
.audio-player-icon-close {
  transform: rotate(180deg);
  font-weight: bolder;
  height: 50px;
  width: 50px;
  font-size: 20px;
  color: #495fef;
  background-color: rgb(42, 42, 42);
  position: absolute;
  left: 10px;
  top: 10px;
  z-index: 1000;
  border-radius: 12px;
  transition: 700ms cubic-bezier(0.54, 0.21, 0.18, 1.35);
  display: flex;
  align-items: center;
  justify-content: center;
}
/* ///////////////////    footer     ///////////////////// */
.footer-rod-container {
  position: absolute;
  width: 150px;
  background-color: transparent;
  bottom: 0;
  right: 0;
  /* width: 100vw; */
  z-index: 1000;
  /* display: flex; */
  /* justify-content: center;
  align-items: center;
  flex-direction: column; */
}
.footer-rod {
  border-radius: 4px 0 0 4px;
  height: 100%;
  width: 100%;
  font-weight: bolder;
  background-color: rgb(42, 42, 42);
  color: white;
  font-size: 10px;
  padding: 2px;
}
/* ///////////////////    MARQUEE     ///////////////////// */

.audio-player-top {
  font-size: 15px;
  width: 300px;
  color: #495fef;
}

.marquee-audio-title {
  color: #495fef;
  width: 270px;
  white-space: nowrap;
}

.audio-subtitle {
  color: white;
  font-size: 11px;
}

@-moz-keyframes scroll-left {
  0% {
    -moz-transform: translateX(100%);
  }
  100% {
    -moz-transform: translateX(-100%);
  }
}

@-webkit-keyframes scroll-left {
  0% {
    -webkit-transform: translateX(50%);
  }
  100% {
    -webkit-transform: translateX(-100%);
  }
}

@keyframes scroll-left {
  0% {
    -moz-transform: translateX(100%);
    -webkit-transform: translateX(100%);
    transform: translateX(90%);
  }
  100% {
    -moz-transform: translateX(-100%);
    -webkit-transform: translateX(-100%);
    transform: translateX(-100%);
  }
}
/* //////////////////////////////////////////// */
.voicenote-save {
  font-size: 14px;
  font-weight: bolder;
  padding: 10px;
}

.media-icon {
  cursor: pointer;
}
.media-icon:hover {
  opacity: 0.5;
}
.audioimportOpenIcon {
  opacity: 1;
  transition: 700ms cubic-bezier(0.54, 0.21, 0.18, 1.35);
  transform-origin: center;
}
.audioimportCloseIcon {
  opacity: 0.4;
  transform: rotate(135deg);
  transform-origin: center;
  transition: 700ms cubic-bezier(0.54, 0.21, 0.18, 1.35);
}
.audioimportOpen {
  height: 400px;
  width: 325px;
  background-color: rgb(42, 42, 42);
  position: absolute;
  left: 10px;
  top: 200px;
  z-index: 1000;
  border-radius: 12px;
  transition: 700ms cubic-bezier(0.54, 0.21, 0.18, 1.35);
  opacity: 1;
}
.audioimportClose {
  visibility: collapse;
  height: 0px;
  width: 325px;
  background-color: rgb(42, 42, 42);
  position: absolute;
  left: 10px;
  top: 200px;
  z-index: 1000;
  border-radius: 12px;
  transition: 700ms cubic-bezier(0.54, 0.21, 0.18, 1.35);
  opacity: 0.6;
}

input[type="range"].volumeSlider {
  width: 100%;
  margin: 5.4px 0;
  background-color: transparent;
  -webkit-appearance: none;
}
input[type="range"].volumeSlider:focus {
  outline: none;
}
input[type="range"].volumeSlider::-webkit-slider-runnable-track {
  background: #495fef;
  /* border: 0.01px solid #000101; */
  border-radius: 25px;
  width: 100%;
  height: 1px;
  cursor: pointer;
}
input[type="range"].volumeSlider::-webkit-slider-thumb {
  margin-top: -5.5px;
  width: 12px;
  height: 12px;
  background: #ffffff;
  border: 0.2px solid rgba(0, 0, 0, 0.7);
  border-radius: 12px;
  cursor: pointer;
  -webkit-appearance: none;
}

input[type="range"].volumeSlider::-moz-range-track {
  background: #495fef;
  border: 0.1px solid #000101;
  border-radius: 25px;
  width: 100%;
  height: 1.2px;
  cursor: pointer;
}
input[type="range"].volumeSlider::-moz-range-thumb {
  width: 12px;
  height: 12px;
  background: #ffffff;
  border: 0.2px solid rgba(0, 0, 0, 0.7);
  border-radius: 9px;
  cursor: pointer;
}
input[type="range"].volumeSlider::-ms-track {
  background: transparent;
  border-color: transparent;
  border-width: 5.4px 0;
  color: transparent;
  width: 100%;
  height: 1.2px;
  cursor: pointer;
}
input[type="range"].volumeSlider::-ms-fill-lower {
  background: #009a82;
  border: 0.1px solid #000101;
  border-radius: 50px;
}
input[type="range"].volumeSlider::-ms-fill-upper {
  background: #495fef;
  border: 0.1px solid #000101;
  border-radius: 50px;
}
input[type="range"].volumeSlider::-ms-thumb {
  width: 12px;
  height: 12px;
  background: #ffffff;
  border: 0.2px solid rgba(0, 0, 0, 0.7);
  border-radius: 9px;
  cursor: pointer;
  margin-top: 0px;
  /*Needed to keep the Edge thumb centred*/
}
input[type="range"].volumeSlider:focus::-ms-fill-lower {
  background: #495fef;
}
input[type="range"].volumeSlider:focus::-ms-fill-upper {
  background: #00f6d0;
}
/*TODO: Use one of the selectors from https://stackoverflow.com/a/20541859/7077589 and figure out
  how to remove the virtical space around the range input in IE*/
@supports (-ms-ime-align: auto) {
  /* Pre-Chromium Edge only styles, selector taken from hhttps://stackoverflow.com/a/32202953/7077589 */
  input[type="range"].volumeSlider {
    margin: 0;
    /*Edge starts the margin from the thumb, not the track as other browsers do*/
  }
}

